:root {
  --heading-font: "PoppinsSemiBold", sans-serif;
  --primary-font: "SatoshiRegular", sans-serif;
}

body {
  display: grid;

  grid-template-rows: 1fr;
  grid-template-columns: 1fr 3fr;
}

#sideBar {
  background-color: rgb(230, 230, 230);
  height: 100vh;
  padding: 15px;
}

#content {
  background-color: rgb(27, 27, 27);
  height: 100vh;
}

#sideBar > h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--heading-font);
  color: rgb(27, 27, 27);
}

#sideBar > h1 {
  font-family: var(--primary-font);
  color: rgb(27, 27, 27);
}

#content > h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--heading-font);
  color: rgb(230, 230, 230);
}

#content > h1 {
  font-family: var(--primary-font);
  color: rgb(230, 230, 230);
}

#sideBarHeader {
  margin-bottom: 15%;
  display: flex;
  align-content: center;
  justify-content: space-between;
}

#sideBarHeader > h1 {
  font-family: var(--heading-font);
  color: rgb(27, 27, 27);
}

#addProject {
  fill: rgb(27, 27, 27);
  width: 50px;
  height: 50px;
  cursor: pointer;
}

#addProject:hover {
  fill: rgb(230, 230, 230);
  background-color: rgb(27, 27, 27);
}

#addProjectFormContainer {
  padding-bottom: 50px;
  border-bottom: 1px solid rgb(27, 27, 27);
}

#addProjectFormContainer > form {
  display: flex;
  flex-direction: column;
  align-items: start;
}

#addProjectFormContainer > form > input,
#addProjectFormContainer > form > textarea {
  margin-bottom: 10px;
}

#addProjectFormContainer > form > .inputContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
}

#addProjectFormContainer > form > .inputContainer > label {
  font-family: var(--heading-font);
  color: rgb(27, 27, 27);
}

#addProjectFormContainer > form > .inputContainer > #projectName {
  font-family: var(--primary-font);
  width: 40%;
}

#addProjectFormContainer > form > #projectDescriptionContainer {
  margin-top: 30px;
}

#addProjectFormContainer > form > .inputContainer > #projectDescription {
  font-family: var(--primary-font);
  width: 100%;
  height: 100px;
  resize: none;
}

#addProjectFormContainer > form > button {
  margin-left: auto;
  margin-top: 15px;

  width: 45%;
  height: 45px;
  padding: 0;
  font-family: var(--primary-font);
  background-color: rgb(27, 27, 27);
  color: rgb(230, 230, 230);
  border: none;

  cursor: pointer;
}

.hidden {
  display: none !important;
}

.project {
  min-height: 15%;
  margin: 50px 0px;
  padding: 5px;
  background-color: rgb(27, 27, 27);

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.projectTitle {
  color: rgb(230, 230, 230);
}

.project > p {
  color: rgb(230, 230, 230);
  font-family: var(--primary-font);
}

.projectActions {
  display: flex;
  justify-content: space-between;
}

.projectActions > svg {
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.projectActions > svg {
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.projectActions > .deleteProject:hover {
  fill: rgb(255, 191, 191);
}

.projectActions > .viewProject:hover {
  fill: rgb(191, 255, 191);
}

#content {
  padding: 15px 30px;
}

.projectHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgb(230, 230, 230);

  margin-bottom: 30px;
}

.projectDetails {
  font-family: var(--heading-font);
}

#addTask {
  width: 50px;
  height: 50px;
  cursor: pointer;
}

#addTask:hover {
  background-color: rgb(230, 230, 230);
  fill: rgb(27, 27, 27);
}

#projectContainer {
  display: flex;
  gap: 30px;
}

#taskListContainer {
  flex: 1.5;
}

#taskForm {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: start;
  width: 100%;

  background-color: rgb(230, 230, 230);
  /* padding: 10px; */
}

#taskForm > input,
#taskForm > textarea {
  margin-bottom: 10px;
}

#taskForm > #deadline {
  width: 100%;
}

#taskForm > .inputContainer {
  display: flex;
  flex-direction: column;
  width: 100%;

  margin: 15px;
}

#taskForm > #priorityContainer {
  display: flex;
  flex-direction: column;

  margin: 15px;
}

#taskForm > .inputContainer > label {
  font-family: var(--heading-font);
  color: rgb(27, 27, 27);
}

#taskForm > .inputContainer > #taskName {
  font-family: var(--primary-font);
  width: 40%;
}

#taskForm > .inputContainer > #taskNotes {
  font-family: var(--primary-font);
  width: 94%;
  height: 250px;
  resize: none;
}

#taskForm > .inputContainer > #deadline {
  width: 40%;
  font-family: var(--primary-font);
}

#priorityContainer > div {
  display: flex;
  flex-direction: row;

  width: 150%;
  justify-content: space-between;
  align-items: center;

  font-family: var(--heading-font);
}

#priorityContainer > div > input[type="radio"] {
  background-color: #fff;
  margin: 0;
  font: inherit;
  color: currentColor;
  width: 0.95em;
  height: 0.95em;
  border: 0.12em solid currentColor;
  border-radius: 50%;

  display: grid;
  place-content: center;
}

#priorityContainer > div > input[type="radio"]::before {
  content: "";
  width: 0.55em;
  height: 0.55em;
  border-radius: 50%;
  transform: scale(0);

  box-shadow: inset 1em 1em rgb(27, 27, 27);
}

#priorityContainer > div > input[type="radio"]:checked::before {
  transform: scale(1);
}

#taskForm > button {
  margin-left: auto;
  margin-bottom: 15px;
  margin-right: 15px;

  width: 45%;
  height: 45px;
  padding: 0;
  font-family: var(--primary-font);
  background-color: rgb(27, 27, 27);
  color: rgb(230, 230, 230);
  border: none;

  cursor: pointer;
}

.taskPriority {
  width: 20px;
  height: 20px;
  border-radius: 50%;
}

.taskPriority.priorityHigh {
  background-color: rgb(255, 122, 122);
}

.taskPriority.priorityMedium {
  background-color: rgb(255, 202, 96);
}

.taskPriority.priorityLow {
  background-color: rgb(119, 255, 119);
}

.taskItem {
  background-color: rgb(230, 230, 230);
  padding: 15px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1rem;
}

.taskItem .taskHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.taskItem h2 {
  font-family: var(--heading-font);
  color: rgb(27, 27, 27);
}

.taskItem p {
  font-family: var(--primary-font);
  color: rgb(27, 27, 27);
}

.taskItem .taskFooter {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.taskItem .taskPriorityContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.taskItem .taskActions {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#deleteTask {
  width: 30px;
  height: 30px;
  cursor: pointer;

  fill: rgb(27, 27, 27);
}

#deleteTask:hover {
  fill: rgb(254, 151, 151);
}
